import { Route, Routes } from "react-router-dom";
import React, { useState, useEffect, lazy, Suspense} from "react";

// styli
import './style.scss';
// components
// import Home from './pages/home/Home.jsx';
import Picture from './pages/picture/Picture.jsx'
import Header from "./components/header/header.jsx";
import Footer from "./components/footer/Footer.jsx";
import Preloader from "./components/preloader/Preloader.jsx";
import Spiner from "./components/preloader/Spiner.jsx";

const Home = lazy(()=> import('./pages/home/Home.jsx'))
function App() {
   // Состояние для отображения прелоудера
   const [preloader, setPreloader] = useState(true);

   useEffect(() => {
     // Имитация задержки загрузки данных
     const fakeDataFetch = () => {
       setTimeout(() => {
         setPreloader(false); // Установка прелоудера в false после истечения задержки
       }, 5500);
     };
     fakeDataFetch();
   }, []);

  

   return (
    <div className="App">
    
    {/* Контент страницы, который отображается после загрузки */}
    
    {!preloader && (
      <>
        <Header />
        <Routes>
          <Route path="/" exact index element={<Suspense fallback={<Spiner/>}><Home /></Suspense>} />
          <Route path="/picture" element={<Picture />} />
        </Routes>
        <Footer />
      </>
    )}
    {/* Условный рендеринг прелоудера */}
    {preloader && (
      <Preloader/>
    )}
  </div>
);
}

export default App;
